<template>
  <div>
    <el-row :gutter="50" justify="center" style="margin-top: 50px">
      <el-col :span="6"
        ><div class="task-button" @click="onTaskClick(0)"><startIcon />开始</div></el-col
      >
      <el-col :span="6"
        ><div class="task-button" @click="onTaskClick(1)"><pauseIcon />暂停</div></el-col
      >
    </el-row>
    <el-row :gutter="50" justify="center" style="margin-top: 50px">
      <el-col :span="6">
        <div class="task-button" @click="onTaskClick(2)"><continueIcon />继续</div></el-col
      >
      <el-col :span="6">
        <div class="task-button" @click="onTaskClick(3)"><endIcon />结束</div></el-col
      >
    </el-row>
  </div>
</template>

<script>
import { ctrlTask } from "@/service/robot";
import { ElMessage } from "element-plus";
import { startIcon,pauseIcon,continueIcon ,endIcon} from "@/icon/index";
export default {
  name: "RobotTask",
  components: {
    startIcon,
    pauseIcon,
    continueIcon,
    endIcon
  },
  methods: {
    async onTaskClick(cmd) {
      const res = await ctrlTask({ cmd, taskType: "delivery_all_same_floor" });
      if (res.code === 0) {
        ElMessage({
          message: `指令发送成功`,
          type: "success",
        });
      }
    },
  },
};
</script>

<style scoped>
.task-button {
  height: 150px;
  border: 1px solid #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.task-button:hover {
  border: 1px solid #adbbff;
  color: #adbbff;
}
</style>