  </div> -->
  <div style="margin-top: 10px">
    <!-- <div>机械臂控制</div> -->
    <el-row :gutter="50" justify="center">
      <el-col :span="6">
        <div @click="onDeleteErrors" class="task-button">清错</div>
      </el-col>
      <el-col :span="6">
        <div @click="controlArm(1, 'enable', true)" class="task-button">
          使能机械臂
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="50" style="margin-top: 50px" justify="center">
