          去使能机械臂
        </div>
      </el-col>
      <el-col :span="6">
        <div @click="controlArm(0, 'powerOn', true)" class="task-button">
          上电
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="50" style="margin-top: 50px" justify="center">
      <el-col :span="6">
        <div @click="controlArm(0, 'powerOn', false)" class="task-button">
          断电
        </div>
      </el-col>
      <el-col :span="6">
        <div @click="onArmBackOrigin" class="task-button">机械臂长按回原点</div>
      </el-col>
    </el-row>

    <!-- <el-button @click="controlArm(1, 'enable', true)">使能机械臂</el-button>
    <el-button @click="controlArm(1, 'enable', false)">去使能机械臂</el-button>
